"use client";

import React, { FormEvent, useState } from "react";
import { Button } from "../../components";
import { Input } from "../../components";
import type { ButtonProps, InputProps } from "../../components";

type FormConfig = {
  form?: {
    className?: string;
    onSubmit?: (inputValue: string, e: FormEvent<HTMLFormElement>) => void;
  };
  input?: Omit<InputProps, "onChange" | "value" | "type" | "id"> & {
    id?: string;
    type?: string;
    placeholder?: string;
    onChange?: (value: string) => void;
  };
  button?: ButtonProps;
};

const SingleFieldForm: React.FC<FormConfig> = (props) => {
  const config = {
    ...defaultConfig,
    ...props,

    form: {
      ...defaultConfig.form,
      ...props.form,
    },
    input: {
      ...defaultConfig.input,
      ...props.input,
    },
    button: {
      ...defaultConfig.button,
      ...props.button,
    },
  };

  const [inputValue, setInputValue] = useState<string>("");

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }
    config.form.onSubmit?.(inputValue, e);
  };

  const handleInputChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    if (e) {
      e.stopPropagation();
    }
    const value = e.target.value;
    setInputValue(value);
    config.input.onChange?.(value);
  };

  return (
    <form className={config.form.className} onSubmit={handleSubmit}>
      <Input value={inputValue} {...config.input} onChange={handleInputChange} />
      <Button type="submit" {...config.button}>
        {config.button.title}
      </Button>
    </form>
  );
};

const defaultConfig: Required<FormConfig> = {
  form: {
    className: "",
    onSubmit: (inputValue: string) => {
      console.log({ inputValue });
    },
  },
  input: {
    id: "email",
    type: "email",
    placeholder: "Enter your email",
    onChange: (value: string) => {
      console.log({ value });
    },
  },
  button: {
    title: "Sign Up",
  },
};

SingleFieldForm.displayName = "SingleFieldForm";

export { SingleFieldForm };
